.bg-oat{
  .stripenav{
    margin-top: 0;
  }
}
.subheader{

  position: relative;
  width: 100%;
  h2 a{
    background-color: #fff;
    padding-right: 16px;
  }
  &:before{
    content: '';
    position: absolute;
    left: 1px;
    right: 0;
    bottom: 12px;
    height: 1px;
    background: #2a2a2a;
    z-index: 0;
  }
}
.stripenav {
  display: inline-block;
  width: 100%;
  direction: ltr;
  overflow: hidden;

  .stripenav-action-btn{
    float: right;
    position: relative;
    background-color: #fff;
  }
  .heading{
    float: left;
  }

  @include mq($from:gel-bp-xs, $until:gel-bp-m) {
  margin-top: $spacing-vertical-small * 4;
  padding-bottom: $gel-alt-spacing-unit;
  }

  @include mq($from:gel-bp-m, $until:gel-bp-l) {
  margin-top: $spacing-vertical-medium * 2;
  padding-bottom: $gel-spacing-unit * 2;

  }
  @include mq($from:gel-bp-l, $until:gel-bp-xxl) {
  margin-top: $spacing-vertical-large * 2;
  padding-bottom: $gel-spacing-unit * 2;

  }
  @include mq($from:gel-bp-xxl) {
  margin-top: $spacing-vertical-xxlarge * 2;
  padding-bottom: $gel-spacing-unit * 2;
  }

> ul{
    white-space: nowrap;
  }

  .icon{
    padding-left: 10px;
    svg{
      width: 19px;
      fill: $ebon;
    }
  }

  .section-info {
    @include mq($from:gel-bp-xs, $until:gel-bp-m) {
      display: none;
    }
    font-size: 14px;
    line-height: 1.75rem;
    color: $rhino;
    background-color: #ffffff;
    z-index: 9;
    position: relative;
    padding-right: 12px;
    top: 2px;
  }
}
.stripenav li {
    display: inline-block;

}

.stripenav h2 {
    @include gel-typography('double-pica');
    color: $ebon;
}
.stripenav .see-all-btn {
  z-index: 999;
  span{
    @include gel-typography('pica-bold');
    @include mq($from:gel-bp-m) {
      line-height: 1.7rem !important;
    }
    padding-left: 16px;
    padding-right: 2px;
  }
}
.stripenav li:first-child h2 a {
    padding-right: 16px;
    position: relative;
}

.stripenav li:nth-child(n+2) {
    margin-left: 16px;
}

.stripenav li:nth-child(n+2) {
    padding-top: .7rem;
}

.stripenav li>a {
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    font-weight: 400;
    letter-spacing: 0.5px;
    color: #000;
}
