///*------------------------------------*\
//   # max
//
//\*------------------------------------*/



.has-cluster{
  padding-bottom: 0;
    @include mq($from: 'gel-bp-xs', $until: 'gel-bp-l') {
  }
}

.max--wrap + .hydrogen--wrap{
    @include mq($from: 'gel-bp-xs', $until: 'gel-bp-m') {
      .promo--media-edge-to-edge {
        margin-top: 16px;
        .promo__media {
          margin-left: 0px;
          margin-right: 0px;
      }
    }
  }
}

.max--box{
  width: 100%;
}

.max--wrap{
    margin-bottom: $spacing-vertical-small * 2;

    @include mq($from: 'gel-bp-xs', $until: 'gel-bp-m') {
      margin-bottom: 0;
    }
    @include mq($from: 'gel-bp-m') {
      padding-top: $spacing-vertical-small * 2;
    }
}
.max__item {
  @include promo--lead (56.25%, $volume-setting:"max");
  .live_promo{
    border-top: 3px solid $postbox;
    background-color: #fff;
    padding: 10px $gel-alt-spacing-unit $gel-spacing-unit halve($gel-alt-spacing-unit);
    box-shadow: 0 7px 0 -5px rgba(210, 210, 210, 0.85);
  }


  @include mq($from: 'gel-bp-xs') {

    &:first-child{
      .live_promo{
        display: inline-block;
        width: 100%;
      }
    }
  }

  @include mq($from: 'gel-bp-m') {
    float: left;
    &:first-child{
      width: gel-columns(12/1) !important;
      .live_promo{
        width: gel-columns(1/3);

      }

    }
  }

  @include mq($from: 'gel-bp-l') {

    &:first-child{
      .live_promo{
          width: calc((#{gel-columns(1/4)} - 12px );

      }
    }
  }

  @include mq($from: 'gel-bp-xxl') {

    &:first-child{

      .live_promo{
        width: calc((#{gel-columns(1/5)} - 10px );
        float: right;
      }
      .has-live-alsos {
        .live_promo{
          width: calc((#{gel-columns(1/5)} - 14px );
        }
      }

    }


  }
}

.max__item{
  .promo__body {
    display: inline-block;
    vertical-align: top;
  }
  @include mq($from:gel-bp-xs, $until:gel-bp-m) {
     .promo__body {
       margin-bottom: 12px;
     }
   }
  .see-also{
    display: flex;
    flex-wrap: wrap;
    @include mq($from: 'gel-bp-xs') {
      padding-top: 0;
      .at-line-b-until\@s:after {
        right: 0px;
        left: 0px;
      }
      width: calc((#{gel-columns(12/1)} + 16px));
      a {padding-top: 0;}

    }
    @include mq($from: 'gel-bp-s') {
      .see-also__item{
        flex: 1 0 calc(#{gel-columns(1/2)} - 16px);
        width: gel-columns(1/2);
      }
    }
    @include mq($from: 'gel-bp-m') {
      .at-line-b-until\@m:after {
        right: 8px;
        left: 8px;
      }

      .see-also__item{
          flex: 1 0 calc(#{gel-columns(1/3)} - 16px);
          width: gel-columns(1/3);
      }
    }
    @include mq($from: 'gel-bp-l') {
        margin-left: -16px;
      .see-also__item{
        flex: 1 0 calc(#{gel-columns(1/6)} - 16px);
        width: gel-columns(1/6);
        padding-top: 0px;
        padding-bottom: 0px;
      }
    }
    @include mq($from: 'gel-bp-xxl') {
        width: gel-columns(1/5);
        margin-left: 0px;
      .see-also__item{
        &:first-child{
          padding-top: 0;
        }
          flex: 1 0 calc(100% - 16px);
          width: gel-columns(12/1);
          margin-left: 0px;
          margin-right: 14px;
          padding-top: 6px;
          padding-bottom: 6px;

      }
    }
  }
}


.max--wrap{
  .has-live-promo{
      .promo__media{
        @include mq($from:gel-bp-xs, $until:gel-bp-m) {
            margin-bottom: 8px !important;
        }
        display: inherit !important;
      }
  }
}


///*------------------------------------*\
//   # MAX CARD
//
//\*------------------------------------*/

.max--card{


  @include mq($from:gel-bp-l, $until:gel-bp-xxl) {
    &.max--inverted{
      .promo--lead {
        padding-right: 0px;
        .see-also{
          margin-left: 0px;
          padding-left: 0px !important;
          padding-right: 24px;
        }
      }

      .promo__body {
        padding-left: 12px;
      }
    }
  }

  @include mq($from:gel-bp-xxl) {
    &.max--inverted{
      .promo--lead {
        padding-right: 0px;
        .see-also{
          margin-left: 12px;
        }
      }

      .promo__body {
        padding-left: 12px;
      }
    }
  }

  .max--box .promo {
    background-color: #fff;

    @include mq($from:gel-bp-xs, $until:gel-bp-m) {

        padding: 8px;
        .promo__media{
          margin: -8px
        }
    }
    @include mq($from:gel-bp-m, $until:gel-bp-l) {

      .promo__body {
        padding-left: 12px;

      }
      .see-also{
        padding-bottom: 12px;
        padding-left: 12px;
        padding-right: 8px;
      }
    }

    @include mq($from:gel-bp-l, $until:gel-bp-xxl) {
        padding-right: 12px;
        .promo__body {
          margin-top: 8px;
        }
      .see-also{
        padding-bottom: 12px;
        padding-left: 8px;
        padding-right: 8px;
      }
    }
    @include mq($from:gel-bp-xxl) {
        padding-right: 16px;
      .promo__media {
        width: calc((60% + 16px));
      }
      .promo__body {
        margin-top: 8px;
        width: calc((40% - 16px));
      }
      .see-also {
        margin-bottom: 8px;
        width: calc((20% - 16px));
        width: -webkit-calc((20% - 16px));
        width: -moz-calc((20% - 16px));
      }
    }
  }
}


///*------------------------------------*\
//   # MAX INVERTED
//
//\*------------------------------------*/


@include mq($from:gel-bp-l) {
  .max--inverted{
    .max__item .promo__media{
      float: right;
      padding-left: 16px;
      padding-right: 0;
    }
    .see-also{
      margin-left: -16px;
    }
  }
}

@include mq($from:gel-bp-xxl) {
  .max--inverted{
    .see-also{
      margin-left: 0px;
    }
  }
}


///*------------------------------------*\
//   # IF MAX Has only 3 items
//
//\*------------------------------------*/


.max__item{
  .promo .promo__item:nth-last-child(n+3):nth-last-child(-n+3):first-child, .promo .promo__item:nth-last-child(n+3):nth-last-child(-n+3):first-child ~ .promo__item {
      @include mq($from:'gel-bp-xs', $until:'gel-bp-m') {
        &.live_promo{
         margin-bottom: 16px;
       }
      }
    @include mq($from:'gel-bp-m', $until:'gel-bp-l') {
    &.see-also{
        padding-top: 8px;
        width: gel-columns(1/2);
        display: inline-block;
        margin-left: 0;
        padding-left: 13px;
        .see-also__item{
          flex: 1 0 calc(#{gel-columns(12/1)} - 16px);
          width: gel-columns(12/1);
          margin-left: 0;
          &:first-child{
            padding-top: 0px;
          }
        }
      }
    &.promo__body {
      width: gel-columns(1/2);
    }
    &.live_promo{
      width: gel-columns(1/2);
    }
  }
  @include mq($from:'gel-bp-l', $until:'gel-bp-xxl') {
  &.promo__media{
    padding-bottom: 0;
  }
  &.see-also{
      width: calc(#{gel-columns(1/4)} - 12px);
      display: inline-block;
      float: right;
      .see-also__item{
        flex: 1 0 calc(#{gel-columns(12/1)});
        width: gel-columns(12/1);
        margin-left: 0;
        &:first-child{
          padding-top: 0px;
        }
        padding-top: 6px;
        padding-bottom: 6px;
      }
    }

  }
  @include mq($from:'gel-bp-xxl') {
    &.live_promo{
        width:  calc(#{gel-columns(2/5)} - 12px);
    }
    &.see-also{
      width:  calc(#{gel-columns(2/5)} - 16px);
    }
  }
}

    .promo .promo__item:nth-last-child(n+4):nth-last-child(-n+4):first-child, .promo .promo__item:nth-last-child(n+4):nth-last-child(-n+4):first-child ~ .promo__item {
       &.live_promo{
        margin-bottom: 12px;
      }
    }
}
