/*------------------------------------*\
    #LIGHT THEME
\*------------------------------------*/
.theme--light{
  background-color: $oat;


  @include mq($from:gel-bp-m, $until:gel-bp-xxl) {
    .iron__groupings .iron__item{
      margin-bottom: 0px;
    }
  }



  .hydrogen--box.at-line-b-until\@m {
    padding-bottom: 0px;
    margin-bottom: 0px;
  }



.stripenav .stripenav-action-btn, .subheader h2 a{
  background-color: $oat;
}

.media-type {
    background-color: $oat;
}

}
