// _on-now.scss
//


.on-now {
  padding: 8px 12px 12px 12px;
  @include mq($from:gel-bp-xxl) {
  min-height: 208px;
  }
  background-color: $oat;
  width: 100%;
  display: flex;
  flex-direction: column;

  .metadata{
    white-space: pre-line;
  }
  .on-now--item{
    margin-top: 8px;
    width: 100%;
    display: flex;
  .promo__content{
    margin-bottom: 8px;
  }

      .live-media, .metadata__time,.metadata__section,.metadata__time{
        margin-top: 0;
      }

    .promo{
        overflow: hidden;

    }
    &:first-child{
      .promo{
        @include mq($from:gel-bp-xs, $until:gel-bp-m) {
            display: table;
       margin-right: 8px;
       padding-right: 8px;
       }
        @include mq($from:gel-bp-m) {
       padding-bottom: 8px;
          }
       &:before{
         right: 0px;
       }

      }
    }
    &:last-child{
      .promo{
        @include mq($from:gel-bp-xs, $until:gel-bp-m) {
        display: table;
       padding-left: 8px;
       }
      }
    }
  }
  .on-now-group{
    display: flex;
    flex-wrap: wrap;
    flex: 1 1 auto;
  }
  .promo{
    min-height: initial !important;
    display: flex;
    .promo__body{
      display: flex;
      justify-content: space-between;
      flex-direction: column;
    }
  }
  .live-media{
    &.video{
      svg{
        vertical-align: middle;
        fill: $postbox;
      }
    }
    &.audio{
      .gs-o-live-status__text{
        color: #002c5f;
      }
      svg{
        fill: #002c5f;
      }
    }
  }

}
