

.label {
  font-size: 0.76em;
  line-height: 1;
  font-weight: bold;
  text-transform: uppercase;
  display: block;
  .label__red-badge .label--breaking &{
    display: table;
    font-size: 0.7em;
    padding: 0.3em 0.45em;
    background-color: $postbox;
    color: #fff;
    -webkit-font-smoothing: antialiased;
  }

  .label__red-badge .max--wrap .label--breaking &{
    @include mq($from:'gel-bp-xxl') {
        font-size: 0.5em;
    }
  }



  .promo--standard &{
    margin-top: 4px;
    margin-bottom: 4px;
  }
  .promo--lead &{
    margin-bottom: 4px;
  }
  .max--wrap &{
    margin-bottom: 4px;
  }
  &:after {
      // font-family: 'custom-symbols' !important;
      // content: '\e904';
  //   background: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAQAAAAGCAYAAADkOT91AAAAAXNSR0IArs4c6QAAABlJREFUCB1jYEAHF5ik/4PEYDS6PEKGeBUA/6cLifZDy04AAAAASUVORK5CYII=');
      color: $postbox;
      opacity: 1;
      font-weight: 400;
      font-size: 0.8em;
      top: 1px;
      position: relative;
  }
  // -- Label Colour
  .label--newsbeat & {

  }

  .label--breaking &{
    color: $postbox;
  }


  // -- Label Structural
  .label--inline &{
    margin-top: -0.25em;
  }


}
