// _markets.scss
//
$markets-positive:#00782c;
$markets-negative: #cc1417;

.markets {
  .market-down{
    .section-name-trunc {
      color: $markets-negative;
    }
  }
  .market-up{
    .section-name-trunc {
      color: $markets-positive;
    }
  }
  .metadata li:after{
    display: none;
  }
  .metadata__time{
    padding-right: 0;
    .icon{
      display: none;
    }
  }

  .promo--feature_brand{
    .icon--type{
      margin-right: 6px;
    }
    .icon--type, .icon--type svg{
        height: 1.5em;
        width: 1.5em;
        vertical-align: text-top;
    }
    .icon + .promo__headline{
      display: inline-block;
    }
  }
  @include mq($from:gel-bp-xs, $until:gel-bp-m) {
    align-items: center;
    .promo__content{
      width: 60%;
    }
    .market--live{
      margin-top: 8px;
      .promo__content{
        width: 100%;
      }
    }
    .promo__body{
      flex-direction:row;
      justify-content: space-between;
    }
    .metadata__time, .metadata__section{
      font-size: 14px;
      margin-top: 2px;
    }
    .metadata{
      padding-left: 8px;
      padding-left: 8px;
      display: flex;
      justify-content: space-between;
      flex-direction: row;
      flex: 1 1 auto;
    }
  }

  @include mq($from:660px, $until:gel-bp-l) {
    align-items: center;

    .promo__body{
      flex-direction:row;
      justify-content: flex-start;
    }
    .metadata__time, .metadata__section{
      font-size: 14px;
      margin-top: 3px;
    }
    .metadata{
      padding-left: 8px;
    }
  }

    @include mq($from:gel-bp-xxl) {
      align-items: center;
      margin-top: -12px;
      .promo__body{
        flex-direction:row;
        justify-content: flex-start;
      }
      .metadata__time, .metadata__section{
        font-size: 14px;
        margin-top: 3px;
      }
      .metadata{
        padding-left: 8px;
      }
    }

    .market--live{
      .promo{
        background-color: $oat;
        padding: 12px;
      }
    }
}
