
///*------------------------------------*\
//    # PROMO FEATURE
//\*------------------------------------*/
@mixin promo--feature{
  background-color: #fff;
    -webkit-font-smoothing:antialiased !important;

  .promo__media{
    margin-bottom: 0;

    .media-type__duration{
      display: none;
    }

    &:before {
    content: "";
    display: block;
    position: absolute;
    background-image: -moz-linear-gradient(top, rgba(0,0,0,0) 10%, rgba(0,0,0,1) 130%);
    background-image: -webkit-linear-gradient(top, rgba(0,0,0,0) 10%,rgba(0,0,0,1) 130%);
    background-image: linear-gradient(to bottom, rgba(0,0,0,0) 10%,rgba(0,0,0,1) 130%);
    width: 100%;
    z-index: 1;
    bottom: 0;
    height: 100%;
    }
    @include mq($from:gel-bp-xs, $until:740px) {
      &:before {
      content: "";
      display: block;
      position: absolute;
      background-image: -moz-linear-gradient(top, rgba(0,0,0,0) 10%, rgba(0,0,0,1) 130%);
      background-image: -webkit-linear-gradient(top, rgba(0,0,0,0) 10%,rgba(0,0,0,1) 130%);
      background-image: linear-gradient(to bottom, rgba(0,0,0,0) 10%,rgba(0,0,0,1) 130%);
      width: 100%;
      z-index: 1;
      bottom: 0;
      height: 100%;
      }
    }

  }

  .promo__body{
    z-index: 2;
    position: absolute;


    .promo__headline{
        @include gel-typography('double-pica-bold');
          -webkit-font-smoothing:antialiased !important;
    }
    a{
      color: #fff;
    }
    .metadata__section{
      @include gel-typography('minion');
    }
    .metadata__time .icon svg, .metadata__comments .icon svg{
      fill: #ededed;
    }
    .metadata {
      margin-top: 0;
      margin-bottom: halve($gel-spacing-unit);
      .section-name-trunc{
        color: #ededed;
      }
      li{
        color: #ededed;
      }
    }
  }
  .promo--feature_brand{
    position: relative;
    display: inline-block;
    overflow: hidden;
    padding-right: $gel-alt-spacing-unit;
    padding-left: $gel-alt-spacing-unit;
    &:after{
      content: "";
      display: block;
      position: absolute;
      left: 0;
      top: halve($gel-spacing-unit);
      height: 100%;
      border-left: 2px solid $bling;
    }
    &:before{
      content: "";
      display: block;
      position: absolute;
      right: 0;
      top: halve($gel-spacing-unit);
      height: 100%;
      border-right: 2px solid $bling;
    }
  }
  .promo__summary{
    color: #D2D2D2;
    position: relative;
    padding-right: $gel-alt-spacing-unit;
    padding-left: $gel-alt-spacing-unit;
    &:after{
      content: "";
      display: block;
      position: absolute;
      left: 0;
      top: - $gel-alt-spacing-unit;
      height: calc(100% + #{$gel-alt-spacing-unit});
      border-left: 2px solid $bling;
    }
  }
    .media-type{
      padding: 8px 8px 7px 8px;
      margin-left: 12px;
      margin-bottom: 38px;
      z-index: 1;
    }
    .promo__body{
      padding-left: $gel-alt-spacing-unit;
      padding-right: $gel-alt-spacing-unit;
    }

    @include mq($from:gel-bp-xs) {

      .promo__body{
        bottom: 16px;
        padding-left: $gel-alt-spacing-unit;
        padding-right: 0;
        .promo__summary{
          display: none;
        }
      }
    }


    @include mq($from:gel-bp-m) {
      .promo--feature_brand{
        width: 100%;
      }
      .promo__body{
        padding-left: $gel-alt-spacing-unit;
        padding-right: $gel-alt-spacing-unit;
        width:  calc( #{gel-columns(1/2)} + #{double($gel-spacing-unit)});
        .promo__summary{
          display: inherit !important;
        }
      }
    }

    @include mq($from:741px) {
      .gel-responsive-image--16by9{
        padding-bottom: 45%;
      }
      .promo__body{

        width:  calc( #{gel-columns(1/3)} + #{double($gel-spacing-unit)});

      }
    }
    @include mq($from:gel-bp-l) {
      .promo__body{

        width:  calc( #{gel-columns(1/3)} + #{double($gel-spacing-unit)});
        .promo__summary{
          display: inherit;
        }
      }
    }


}

@include mq($from:gel-bp-xs) {
  .promo--feature{
    @include promo--feature;
  }
}

@mixin promo--feature__standard{
  background-color: #fff;
  margin-bottom: 0;
  -webkit-font-smoothing:subpixel-antialiased !important;
  @include mq($from:gel-bp-xs) {
  padding-bottom: 56.25%;
  }
  @include mq($from:gel-bp-m) {
  margin-top: 0;
  margin-bottom: 0;
  padding-bottom: 100%;
  }
    .media-type__duration{
      display: none;
    }

      &:before {
      content: "";
      display: block;
      position: absolute;
      background-image: -moz-linear-gradient(top, rgba(0,0,0,0) 10%, rgba(0,0,0,1) 110%);
      background-image: -webkit-linear-gradient(top, rgba(0,0,0,0) 10%,rgba(0,0,0,1) 110%);
      background-image: linear-gradient(to bottom, rgba(0,0,0,0) 10%,rgba(0,0,0,1) 110%);
      width: 100%;
      z-index: 1;
      bottom: 0;
      height: 100%;
      }
  .promo__body{
    z-index: 2;
    position: absolute;
    bottom:0;
    padding: $gel-alt-spacing-unit;
    @include mq($from:gel-bp-xs, $until:gel-bp-m) {
      padding-left: $gel-spacing-unit;
      padding-right: $gel-spacing-unit;
    }
    width: 100%;

    a{
      color: #fff;

    }
    .metadata__section{
      @include gel-typography('minion');
    }
    .metadata__time .icon svg, .metadata__comments .icon svg{
      fill: #ededed;
    }
    .metadata {
      margin-top: 0;
      margin-bottom: halve($gel-spacing-unit);
      .section-name-trunc{
        color: #ededed;
      }
    }
  }
  .promo--feature_brand{
    position: relative;
    display: inline-block;
    overflow: hidden;
    padding-right: $gel-alt-spacing-unit;
    padding-left: $gel-alt-spacing-unit;
    width: 100%;
    &:after{
      content: "";
      display: block;
      position: absolute;
      left: 0;
      top: halve($gel-spacing-unit);
      height: 100%;
      border-left: 2px solid $bling;
    }
    &:before{
      content: "";
      display: block;
      position: absolute;
      right: 0;
      top: halve($gel-spacing-unit);
      height: 100%;
      border-right: 2px solid $bling;
    }
  }
  .promo__headline{
      -webkit-font-smoothing:antialiased !important;
  }
  .promo__summary{

    color: #D2D2D2;
    position: relative;
    padding-right: $gel-alt-spacing-unit;
    padding-left: $gel-alt-spacing-unit;
    &:after{
      content: "";
      display: block;
      position: absolute;
      left: 0;
      top: - $gel-alt-spacing-unit;
      height: calc(100% + #{$gel-alt-spacing-unit});
      border-left: 2px solid $bling;
    }
  }

}

@include mq($from:gel-bp-xs) {
  .promo--feature__standard{
    @include promo--feature__standard;
  }
}
