.sponsorship {
  z-index: 1;
  margin-bottom: 0;
  display: flex;
  justify-content: space-between;

  @include mq($from: 'gel-bp-xs', $until:420px) {
  flex-direction: column;
  }

  li {
   vertical-align: middle;
   padding-top: 8px;
   @include gel-typography('brevier');
    text-transform: inherit;
    position: relative;
    margin-right: 6px;

    &:last-child {
      margin-right: 0;
      padding-right: 0;
      display: flex;
      flex-direction: column-reverse;
      justify-content: space-around;
    }
  }

}

  .sponsorship__copy {
    color: $rhino;
    -webkit-font-smoothing: antialiased;
    display: flex;
    flex-direction: column-reverse;
    .gel-minion{
      text-transform: inherit;
    }
  }
  .sponsorship__brand {
    display: inline;
    .logo{
      display: block;
     max-width:88px;
     max-height:31px;
     width: auto;
     height: auto;

    }
  }
